import { h } from 'preact';
import { isString } from '@ch1/utility';
export function Select({ onChange, options, selected, }) {
    const change = e => onChange(parseInt(e.target.value, 10));
    return (h("select", { onChange: change }, options.map((option, index) => {
        if (isString(option)) {
            if (selected === index) {
                return (h("option", { value: index, selected: true }, option));
            }
            return h("option", { value: index }, option);
        }
        if (selected === index) {
            return (h("option", { value: option.index, selected: true }, option.name));
        }
        return h("option", { value: option.index }, option.name);
    })));
}
