import Router from 'preact-router';
import { Component, h, render as preactRender } from 'preact';
import { LineGraph } from './feature-line-graph/line-graph';
import { createState, loadState, saveState } from './state';
import { selectData, fetchData } from './data';
export class App extends Component {
    constructor() {
        super();
        let state = loadState();
        if (!state) {
            state = createState();
        }
        this.state = state;
        this.selectAndUpdate();
    }
    updateSelectState({ countries, series, }) {
        this.setState(Object.assign(Object.assign({}, this.state), { countries, currentSeries: series }));
    }
    selectAndUpdate() {
        this.state.dataPromise
            .then(() => selectData(this.state))
            .then(this.updateSelectState.bind(this))
            .then(() => saveState(this.state));
    }
    lineGraphState(lgs) {
        this.setState(Object.assign(Object.assign({}, this.state), { lineGraphState: lgs }));
        this.selectAndUpdate();
    }
    reload() {
        this.setState({
            dataPromise: fetchData().then(d => {
                this.selectAndUpdate();
                return d;
            }),
        });
    }
    render() {
        return (h(Router, null,
            h(LineGraph, { path: '/', countries: this.state.countries, currentSeries: this.state.currentSeries, onChange: this.lineGraphState.bind(this), reload: this.reload.bind(this), state: this.state.lineGraphState })));
    }
}
export function render(root) {
    preactRender(h(App, null), root);
}
