import { Component, h } from 'preact';
import { Chart } from '../components/chart';
import { Select } from '../components/select';
import { SelectMultiple } from '../components/select-multiple';
import { flex, flexCol, fullSize, flexItem20, flexItem60 } from '../constants';
import { InputDate } from '../components/date';
import { Button } from '../components/button';
const dataSets = ['Confirmed', 'Deaths', 'Recoveries'];
const modes = ['By date', 'By first confirmed', 'By first 100 confirmed'];
export class LineGraph extends Component {
    selectMode(mode) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { mode }));
    }
    selectDate(ymdString) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { startDate: ymdString }));
    }
    selectDataSets(dataSetIndexes) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { dataSetIndexes }));
    }
    selectCountries(countryIndexes) {
        this.props.onChange(Object.assign(Object.assign({}, this.props.state), { countryIndexes }));
    }
    render() {
        return (h("section", { className: `${fullSize} ${flexCol}` },
            h(Chart, { flexSize: flexItem60, series: this.props.currentSeries }),
            h("section", { className: `${flex} ${flexItem20}` },
                h(Select, { onChange: this.selectMode.bind(this), options: modes, selected: this.props.state.mode }),
                h(InputDate, { onChange: this.selectDate.bind(this), ymdString: this.props.state.startDate }),
                h(SelectMultiple, { onChange: this.selectDataSets.bind(this), options: dataSets, selected: this.props.state.dataSetIndexes }),
                h(SelectMultiple, { onChange: this.selectCountries.bind(this), options: this.props.countries, selected: this.props.state.countryIndexes }),
                h(Button, { classes: ['green'], label: "Reload", onClick: this.props.reload }))));
    }
}
