import { Component, h } from 'preact';
export class Chart extends Component {
    constructor() {
        super();
        const chartDiv = window.document.createElement('div');
        if (!chartDiv) {
            throw new Error('could not add the chart to the DOM');
        }
        this.state = { chartDiv };
    }
    componentDidMount() {
        this.base.id = 'chartDiv';
    }
    componentDidUpdate() {
        JSC.Chart('chartDiv', {
            xAxis_label_text: this.props.useDays ? 'Day' : undefined,
            xAxis: {
                scaleType: 'time',
                customTicks: [
                    {
                        // A tick for every month.
                        value: {
                            month: '*',
                        },
                        label_text: '%min',
                    },
                    {
                        // every month
                        value: { week: '*' },
                        label_text: '%min',
                    },
                ],
            },
            legend: {
                template: '%icon %name',
            },
            series: this.props.series,
        });
    }
    render() {
        return h("div", { className: this.props.flexSize });
    }
}
